const ulElement = document.querySelector(".gallery");
const itemTemplate=({preview, original, description}) =>{
  return `<li class="gallery-item">
    <a class="gallery-link" href="${original}">
      <img
        class="gallery-image"
        src="${preview}"
        alt="${description}"
        
      />
    </a>
  </li>`;    
}
function itemsTemplate(items) {
  return items.map(itemTemplate).join("");
}
const markup = itemsTemplate(images);
ulElement.innerHTML = markup;
let gallery = new SimpleLightbox('.gallery a',{captionsData: "alt", captionPosition:"bottom", captionDelay:250});